<template>
  <div>
    <b-container>
      <h1 class="mt-5 text-primary">[NL052]</h1>
      <p>
        
      </p>
      <div class="text-center">
        <b-button variant="info" class="button-pickup" @click="pickItem" :disabled="pickableItemsList.length === 0"><b-icon-box-arrow-up/></b-button>
        <b-button variant="danger" v-b-modal.resetModal><b-icon-x-circle/></b-button>
        <b-modal id="resetModal" title="" cancel-title="" ok-variant="danger" ok-title="" @ok="resetItems">
          <p class="my-4"></p>
        </b-modal>
      </div>
      <h2 class="mt-3 text-success"></h2>
      <div class="items">
        <items-element v-for="item in items" :item="item" :pickedItemsList="history" :key="item.id" />
      </div>
      <h2 class="mt-3 text-success"></h2>
      <b-list-group>
        <b-list-group-item v-for="(itemID, key) in reversedHistory" :key="key" :variant="key === 0 ? 'info' : ''">
          {{ reversedHistory.length - key }}:
          <img class="history_icon" :src="items[itemID].src">
          <span>{{ items[itemID].name }}</span>
        </b-list-group-item>
      </b-list-group>
    </b-container>
  </div>
</template>
<script>
import ItemsElement from '@/components/EastIndiaCompany/ItemsElement.vue'

const localStorageName = 'east_india_company_items'

export default {
  name: 'EastIndiaCompany',
  components: {
    ItemsElement
  },
  data: function() {
    return {
      items: [
        {
          id: 0,
          name: '',
          src: require('@/assets/img/grain.png')
        },
        {
          id: 1,
          name: '',
          src: require('@/assets/img/vegetable.png')
        },
        {
          id: 2,
          name: '',
          src: require('@/assets/img/sheep.png')
        },
        {
          id: 3,
          name: '',
          src: require('@/assets/img/boar.png')
        },
        {
          id: 4,
          name: '',
          src: require('@/assets/img/cattle.png')
        },
        {
          id: 5,
          name: '',
          src: require('@/assets/img/wood.png')
        },
        {
          id: 6,
          name: '',
          src: require('@/assets/img/clay.png')
        },
        {
          id: 7,
          name: '',
          src: require('@/assets/img/reed.png')
        },
        {
          id: 8,
          name: '',
          src: require('@/assets/img/stone.png')
        }
      ],
      history: []
    }
  },
  computed: {
    pickableItemsList: function() {
      let allItemsList = this.items.map(item => item.id)
      return allItemsList.filter(id => ! this.history.includes(id), this)
    },
    reversedHistory: function() {
      return this.history.slice().reverse()
    }
  },
  created: function() {
    this.history = JSON.parse(localStorage.getItem(localStorageName)) || []
  },
  methods: {
    pickItem: function() {
      let pickedItemID = this.getRandomElement(this.pickableItemsList)
      this.history.push(pickedItemID)
      localStorage.setItem(localStorageName, JSON.stringify(this.history))
      this.$bvToast.toast(`${this.items[pickedItemID].name}`, {
        variant: 'success',
        autoHideDelay: 1500
      })
    },
    resetItems: function() {
      this.history = []
      localStorage.removeItem(localStorageName)
      this.$bvToast.toast('', {
        variant: 'danger',
        autoHideDelay: 1500
      })
    }
  }
}
</script>
<style scoped>
.button-pickup {
  margin-right: 1em;
}
.items {
  display: flex;
  flex-wrap: no-wrap;
  justify-content: center;
}
.history_icon {
  height: 1.5rem;
  margin: 0 0.5rem;
}
</style>
